@import url('https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&family=Lexend+Deca&display=swap');

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

//primary colors
$bright-orange: rgb(227, 136, 38);
$dark-cyan: rgb(0, 105, 112);
$very-dark-cyan: rgb(0, 66, 65);

$transparent-white: rgba(255, 255, 255, 0.75); //paragraphs
$very-light-gray: rgb(242, 242, 242);//background, headings, buttons

//font families
$font-family-1: 'Big Shoulders Display',Arial, Helvetica, sans-serif;
$font-family-2: 'Lexend Deca',Arial, Helvetica, sans-serif;

//mixins responsive
@mixin media-md {
    @media screen and (min-width: 768px){
        @content;
    }
}
@mixin media-lg {
    @media screen and (min-width: 1024px) {
        @content;
    }
}
    
